import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import { changeCount, minusItem } from "./../store"





function Cart() {


    const user = localStorage.getItem('userId');
    const cart = useSelector((state) => { return state.cart })
    const dispatch = useDispatch();//store.js   
    return (
        <div>

            <h6>{user}  </h6>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cart.map((e, i) => {
                            return (
                                < tr >
                                    <td>{e.id}</td>
                                    <td>{e.name}</td>
                                    <td>{e.count}</td>
                                    <td><button onClick={() => {
                                        dispatch(changeCount(e.id))
                                    }}>+</button></td>
                                    <td><button onClick={() => {
                                        dispatch(minusItem(e.id))
                                    }}>X</button></td>
                                </tr>
                            )

                        })
                    }

                </tbody>

            </Table>
        </div >
    )
}

export default Cart;